@import url(https://fonts.googleapis.com/css?family=Kalam:300,400,700&display=swap);
.header {
  padding: 0.75rem 10.625rem;
  display: flex;
  justify-content: space-between;
  background: #FFFFFF;
  box-shadow: 0px 3px 11px -11px rgba(0, 0, 0, 0.08);
  left: 0;
  right: 0; }
  .header .header-name {
    font-size: 1.25rem;
    line-height: 2rem;
    letter-spacing: 0.0195rem;
    color: #787878; }
    .header .header-name .header-name-first {
      color: #3340FF; }
  .header nav {
    display: inline; }
    .header nav a {
      margin-left: 6.5rem;
      font-size: 1.25rem;
      line-height: 2rem;
      text-align: justify;
      letter-spacing: 0.0195rem;
      text-decoration: none;
      color: #787878; }
    .header nav .active {
      color: #3340FF; }

.about {
  display: grid;
  grid-template-columns: 50% auto; }
  .about .about-desc .about-desc-title {
    font-weight: bold;
    font-size: 3.5rem;
    letter-spacing: 0.1407rem;
    color: #000000; }
  .about .about-desc .about-desc-more {
    font-weight: 300;
    font-size: 1.625rem;
    line-height: 2.875rem;
    margin-top: 2rem;
    letter-spacing: 0.0313rem;
    color: #787878; }

.about-parent {
  padding: 4rem 10.625rem; }
  .about-parent img {
    width: 100%; }

.about-title {
  margin-top: 8.75rem;
  margin-bottom: 2.875rem; }
  .about-title h2 {
    margin-bottom: 0;
    padding-bottom: 0; }

.home {
  background: url(/static/media/background.30ae2973.png);
  background-size: cover;
  padding: 5.3125rem  10.625rem; }

.home-landing {
  display: grid;
  grid-template-columns: 49% auto;
  grid-column-gap: 20%; }
  .home-landing .home-landing-title {
    font-weight: bold;
    font-size: 4rem;
    line-height: 4.625rem;
    letter-spacing: 0.1407rem;
    color: #000000; }
  .home-landing .home-landing-desc {
    padding-top: 2.375rem;
    font-style: normal;
    font-weight: 300;
    font-size: 1.875rem;
    line-height: 2.875rem;
    letter-spacing: 0.0313rem;
    color: #787878; }
  .home-landing .home-landing-work {
    border: 0.125rem solid #3340FF;
    border-radius: 0.4375rem;
    display: inline-block;
    font-size: 1.25rem;
    line-height: 2.875rem;
    letter-spacing: 0.0125rem;
    padding: 0.0625rem 2.5rem;
    color: #3340FF;
    margin-top: 3.5rem; }
  .home-landing img {
    width: 80%; }

.home-work {
  margin-top: 7.5rem; }
  .home-work .home-work-unit {
    margin-top: 5rem;
    display: grid;
    grid-template-columns: 38% auto;
    grid-column-gap: 10%;
    position: relative; }
    .home-work .home-work-unit img {
      width: 100%; }
    .home-work .home-work-unit .home-work-title {
      font-weight: bold;
      font-size: 2.125rem;
      line-height: 4.625rem;
      letter-spacing: 0.0125rem;
      color: #151515; }
    .home-work .home-work-unit .home-work-desc {
      font-weight: 300;
      font-size: 1.4375rem;
      line-height: 2.0625rem;
      letter-spacing: 0.0131rem;
      color: #787878; }
    .home-work .home-work-unit .home-work-medium a {
      font-weight: 300;
      font-size: 1.375rem;
      line-height: 2.6875rem;
      letter-spacing: 0.0484rem;
      color: #3340FF;
      position: absolute;
      bottom: 0;
      display: block;
      width: 100%;
      text-decoration: none; }

body {
  font-family: 'Kalam', cursive; }

html {
  font-size: 1.1vw;
  overflow-x: hidden; }

.title {
  display: block;
  border-bottom: 1px solid #B9B9B9;
  font-size: 1.25rem;
  line-height: 2.875rem;
  letter-spacing: 0.0125rem; }
  .title span {
    color: #3340FF; }

a {
  text-decoration: none; }

footer {
  padding: 3em 0;
  background: #FFFFFF;
  border-top: 1px solid #BCBCBC;
  display: grid;
  align-items: center;
  justify-items: center; }
  footer div {
    font-size: 2rem;
    line-height: 4.625rem;
    text-align: center;
    letter-spacing: 0.0748rem;
    color: #787878; }

